body {
  margin: 0;
  padding: 0;
  font-family: "Courier New", Courier, monospace;
  background-image: "octo.png";
  background-repeat: no-repeat;
  background-size: cover;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: left;
  height: 100vh;
  max-width: 400px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 50%;
  max-width: 600px;
  box-sizing: border-box;
}

label {
  color: #00ff00;
  font-weight: bold;
  margin-bottom: 5px;
}
.label-title {
  text-align: left;
  color: #00ff00;
  font-weight: bold;
  margin-bottom: 5px;
}

input[type="text"],
input[type="file"],
textarea {
  background-color: #222;
  color: #00ff00;
  border: 2px solid #00ff00;
  padding: 10px;
  margin-top: 5px;
  width: calc(100% - 22px);
  box-sizing: border-box;
  font-size: 16px;
  text-align: left;
}

input[type="submit"] {
  background-color: #cc3300;
  color: rgb(234, 11, 11);
  border: none;
  padding: 10px 20px;
  margin-top: 20px;
  cursor: pointer;
  border-radius: 5px;
  font-size: 16px;
  font-weight: bold;
}

input[type="submit"]:hover {
  background-color: #ff6600;
}

p {
  margin-top: 20px;
  color: #00ff00;
}

textarea {
  resize: vertical;
  min-height: 100px;
  max-height: 300px;
  width: 100%;
  font-size: 16px;
}

@media screen and (min-width: 768px) {
  .App {
    max-width: 75%;
  }
}

.window {
  background-color: #000000;
  border: 2px solid #00ff00;
  border-radius: 10px;
  width: 100%;
  box-sizing: border-box;
  height: 500px;
  position: relative;
}

.title-bar {
  display: flex;
  align-items: left;
  justify-content: space-between;
  padding: 5px;
  background-color: #2c2b2e;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.title-bar-text {
  color: rgb(255, 255, 255);
  font-weight: bold;
  font-size: 16px;
  margin-left: 1px;
}

.title-bar-controls {
  display: flex;
}

.title-bar-button {
  width: 15px;
  height: 15px;
  margin: 0 5px;
  border-radius: 50%;
}

.title-bar-button.minimize {
  background-color: #ffcc00;
}

.title-bar-button.maximize {
  background-color: #33cc33;
}

.title-bar-button.close {
  background-color: #ff3333;
}

.window-body {
  height: calc(100% - 30px);
  overflow-y: auto;
}

.terminal-text {
  color: #00ff00;
  font-size: 20px;
  line-height: 1.4;
  margin-bottom: 10px;
}
.input-line input[type="text"] {
  background-color: black;
  color: #00ff00;
  border: 1px solid #00ff00;
  border-radius: 4px;
  padding: 8px;
  margin-top: 5px;
  width: 98%; /* Adjust this value as necessary */
  box-sizing: border-box;
}

.input-line textarea {
  flex-grow: 1;
  background-color: black;
  color: #00ff00;
  border: 1px solid #00ff00;
  border-radius: 4px;
  padding: 1px;
  margin-top: 5px;
  resize: vertical;
  min-height: 100px;
  max-height: 300px;
  width: 98%; /* Adjust this value as necessary */
  box-sizing: border-box;
}
